*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  min-height: 100vh;
  margin: 0;
  padding: 0; }

html {
  font-family: "Lato", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  font-style: normal;
  line-height: 18px;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility; }

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  background-color: #ffffff; }

main {
  flex-grow: 1; }

a {
  text-decoration: none;
  color: #ffffff; }

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%); }

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 60px; }
  @media (max-width: 1023px) {
    .container {
      padding: 0 24px; } }
  @media (max-width: 767px) {
    .container {
      padding: 0 20px; } }

.title {
  font-size: 18px;
  font-weight: 600;
  line-height: 22px;
  width: fit-content;
  margin: 0;
  color: #ffffff; }

.btn {
  font-size: 12px;
  font-weight: 700;
  line-height: 14px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: fit-content;
  padding: 0;
  cursor: pointer;
  transition: 0.3s ease;
  color: #ffffff;
  border: none;
  outline: none;
  background-color: rgba(255, 255, 255, 0); }
  @media (hover: hover), screen and (min-width: 0\0) {
    .btn:hover {
      opacity: .5; } }
  @media (hover: hover), screen and (min-width: 0\0) {
    .btn--hover-blue:hover {
      opacity: 1; }
      .btn--hover-blue:hover svg {
        color: #53CBF1; } }
  @media (hover: hover), screen and (min-width: 0\0) {
    .btn--hover-sunset:hover {
      opacity: 1; }
      .btn--hover-sunset:hover svg {
        color: #CA4C4C; } }
  .btn--round-blue-border {
    padding: 10px;
    border: 1px solid #53CBF1;
    border-radius: 100px; }
    @media (hover: hover), screen and (min-width: 0\0) {
      .btn--round-blue-border:hover {
        opacity: 1;
        color: #ffffff;
        background-color: #53CBF1; } }
  .btn--round-blue {
    padding: 10px;
    color: #ffffff;
    border: 1px solid #53CBF1;
    border-radius: 100px;
    background-color: #53CBF1; }
    @media (hover: hover), screen and (min-width: 0\0) {
      .btn--round-blue:hover {
        opacity: 1;
        color: #ffffff;
        background-color: #424242; } }
  .btn svg {
    flex-shrink: 0;
    width: 100%;
    height: 100%;
    transition: color 0.3s ease;
    color: #8D8D8D; }

.input {
  display: flex;
  align-items: center;
  justify-content: space-between; }
  .input label {
    font-size: 12px;
    font-weight: 700;
    line-height: 20px;
    height: fit-content;
    letter-spacing: .01em;
    color: #BFBFBF; }
  .input input,
  .input select {
    font-size: 12px;
    font-weight: 400;
    line-height: 20px;
    display: flex;
    align-items: center;
    flex-grow: 1;
    max-width: 175px;
    min-height: 40px;
    padding: 9px 15px;
    letter-spacing: .01em;
    color: #ffffff;
    color: #BFBFBF;
    border: 1px solid #5F5F5F;
    border-radius: 6px;
    background-color: #424242; }
  .input select {
    padding: 11px 15px; }
  .input--rgb input {
    flex-shrink: 1;
    width: calc((100% - 20px) / 3);
    min-height: 20px;
    padding: 5px; }
    .input--rgb input:not(:first-of-type) {
      margin-left: 10px; }

.modal {
  display: none;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(2px); }
  .modal.active {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center; }
  .modal__wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    padding: 20px 15px 15px 15px;
    color: #ffffff;
    border-radius: 20px;
    background-color: #313131; }

.main {
  padding: 60px 0; }

